<template>
	<div>
		<h1>home</h1>

		<button @click="toMenu" class="btn btn-success">点餐</button>
	</div>

</template>
<script type="text/javascript">
	export default{
		methods:{
			toMenu(){
				//跳转上一次
				//this.$router.go(-1)
				
				//跳转到指定路由
				//this.$router.replace('/menu')
				

				//跳转到指定名字路由
				//this.$router.replace({name:'menuLink'})


				//push 跳转 堆跳转
				
				this.$router.push({name:'menuLink'})

			}
		}
	};
</script>


