/*eslint-disable*/
import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Menu from '@/components/Menu'
import Admin from '@/components/Admin'
import About from '@/components/about/About'
import Login from '@/components/Login'
import Register from '@/components/Register'

//二级路由

import History from '@/components/about/History'
import Delivery from '@/components/about/Delivery'
import Ordering from '@/components/about/Ordering'
import Contact from '@/components/about/Contact'
Vue.use(Router)

export default new Router({

	//路由菜单
	routes: [
		{
			path: '/',
			name: 'homeLink',
			components:{
				default:Home,
				delivery:Delivery,
				history:History,
			}
		},
		{

			path: '/menu',
			name: 'menuLink',
			component: Menu
		},
		{
			path:'/admin',
			name:'adminLink',
			component:Admin,
			//路由独享守卫
			/*beforeEnter:(to,from,next)=>{
				console.log('路由独享守卫')
			}*/
		},
		{
			path:'/about',
			name:'aboutLink',
			component:About,
			children:[
				{
					path:'/about/History',
					name:'historyLink',
					component:History,
				},
				{
					path:'/about/Delivery',
					name:'deliveryLink',
					component:Delivery
				},
				{
					path:'/about/Ordering',
					name:'orderingLink',
					component:Ordering
				},
				{
					path:'/about/Contact',
					name:'contactLink',
					component:Contact
				}
			],
		},
		{
			path:'/login',
			name:'loginLink',
			component:Login
		},
		{
			path:'/register',
			name:'registerLink',
			component:Register,
		},

		//未匹配导航 跳转到首页
		{
			path:'*',
			redirect:'/'
		}

	],


	mode:'history',


	//路由守卫（全局守卫）


})




